import Vue from 'vue'
import Router from 'vue-router'
const Web = () => import(/* webpackChunkName: "Web" */ '@/components/Web.vue');
const Apps = () => import(/* webpackChunkName: "Apps" */ '@/components/Apps.vue');

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Web',
      component: Web
    },
    {
      path: '/apps',
      name: 'Apps',
      component: Apps
    }
  ]
})



